(function() {
  'use strict';

  angular
    .module('app.comments.navbar', [])
    .directive('topbar', topbar)
    .directive('sidebar', sidebar);

    function topbar() {
        return {
            restrict: 'E',
            templateUrl: 'app/components/navbar/navbar.html',
            controller: function() {
            },
            controllerAs: 'vm',
            bindToController: true
        };
    }

  function sidebar() {
      return {
          replace: true,
          restrict: 'AE',
          templateUrl: 'app/components/navbar/sidebar.html',
          controllerAs: 'vm',
          controller: function ($scope, $rootScope, navbarConfig) {
              var vm = this;
              vm.items = navbarConfig.sidebar();
          }
      };
  }

})();

(function() {
    'use strict';

    angular.module('app.comments.navbar')
    .provider('navbarConfig', function() {
        var sidebar = [];
        this.sidebar = function(items) {
            angular.forEach(items, function (item) {
                sidebar.push(item);
                angular.forEach(item.items, function (sub) {
                    //FIXME: cahnge by index or boolean and convert to 30px in html
                    sub.indent = 30;
                    sidebar.push(sub);
                });
            });
        };

        this.$get = function() {
            return {
                sidebar: function() {
                    return sidebar;
                }
            };
        };
    });
})();

(function() {
    'use strict';

    angular.module('app.form1', [])
    .constant('Form1Titles', {
        main:'Form #1',
        second: 'This is the form number 1 inside a simple panel'
    });

})();

(function() {
    'use strict';

    angular.module('app.form1')
    .service('Form1Service', function() {
        this.process = function(form) {
            return 'Value1 is: @value1 and Value2 is: @value2'
                .replace('@value1', form.value1)
                .replace('@value2', form.value2);
        };
    });
})();

(function() {
    'use strict';

    angular.module('app.form1')
    .controller('Form1Controller', function(Form1Titles, Form1Service) {
        var vm = this;

        vm.main = Form1Titles.main;
        vm.second = Form1Titles.second;

        vm.process = function() {
             vm.processed = Form1Service.process(vm.form);
        };
    });
})();

(function() {
  'use strict';

  angular
    .module('app', [
        'app.comments.navbar',
        'app.form1',
        'ngplus',
        'ngAnimate',
        'ngSanitize',
        'ngMessages',
        'ngResource',
        'ui.router',
        'ui.bootstrap',
        'toastr'
    ]);

})();

(function() {
  'use strict';

  angular
    .module('app')
    .run(runBlock);

  /** @ngInject */
  function runBlock($log) {

    $log.debug('runBlock end');
  }

})();

(function() {
  'use strict';

  angular
    .module('app')
    .config(routerConfig);

  /** @ngInject */
  function routerConfig($stateProvider, $urlRouterProvider) {
    $stateProvider
      .state('main', {
        url: '/',
        templateUrl: 'app/main/main.html'
      })
      .state('main.form', {
          url:'form',
          templateUrl: 'app/form1/form1.html',
          controller: 'Form1Controller',
          controllerAs: 'vm'
      })
      .state('main.form2', {
          url:'form2',
          template: '<span>HI FORM 2!</span>'
      });

    $urlRouterProvider.otherwise('/');
  }

})();

/* global malarkey:false, moment:false */
(function() {
  'use strict';

  angular
    .module('app')
    .constant('malarkey', malarkey)
    .constant('moment', moment);

})();

(function() {
  'use strict';

  angular
    .module('app')
    .config(config);

  /** @ngInject */
  function config($logProvider, toastrConfig, navbarConfigProvider) {
    // Enable log
    $logProvider.debugEnabled(true);

    // Set options third-party lib
    toastrConfig.allowHtml = true;
    toastrConfig.timeOut = 3000;
    toastrConfig.positionClass = 'toast-top-right';
    toastrConfig.preventDuplicates = true;
    toastrConfig.progressBar = true;

    navbarConfigProvider.sidebar([{
        name: 'Packages',
        state: 'package',
        icon: 'fa fa-file-o'
    }, {
        name: 'Executions',
        state: 'execution',
        icon: 'fa fa-gears'
    }, {
        name: 'Settings',
        icon: 'fa fa-gear',
        items: [{
            name: 'Packages',
            state: 'settings-packages',
            icon: 'glyphicon glyphicon-plus'
        }, {
            name: 'Datasources',
            state: 'servers',
            icon: 'fa fa-plug'
        }, {
            name: 'Mappings',
            state: 'mappings',
            icon: 'fa fa-arrows-h'
        }]
    }]);
  }

})();

